




























































































import Vue from 'vue';
import { createSimpleTransition } from 'vuetify/src/util/helpers';
import RomaUnit from './RomaUnit.vue';
import RomaUnitModalForm from './RomaUnitModalForm.vue';
import {mapState} from 'vuex';

const romaUnitListTransition = createSimpleTransition('unit-list-transition');

Vue.component('my-transition', romaUnitListTransition);

export default Vue.extend({
    name: 'RomaUnitList',
    props: {
        value: {
            type: Object,
        },
    },
    data() {
        return {
            schooling: null,
            showUnitModalForm: false,
        };
    },
    methods: {
        editUnit(unit) {
            this.$refs.unitModalForm.request = unit;
            this.showUnitModalForm = true;
        },
        delUnit(unit: object) {
            const data = {
                unit,
                school: this.value,
            };
            this.$store.commit('delUnit', data);
        },
        selectUnit(unit) {
            // this.$emit('selectUnit', unit);
            this.$router.push({
                name: 'organizer-unit',
                params: {id: unit.id},
            });

        },
    },
    components: {
        RomaUnit,
        RomaUnitModalForm,
    },
});
